import React, { useState } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import MainSideNav from "./components/sidebar/MainSideNav";
// import Button from './components/Button.js'
// import Header from './components/Header.js'
// import Sidebar from './components/Sidebar.js'
const Topic = React.lazy(() => import("./components/Topic"));
const Quiz = React.lazy(() => import("./components/Quiz"));
const Button = React.lazy(() => import("./components/Button"));

function App() {
  return (
    <Router>
      <MainSideNav></MainSideNav>
      <div>
        <React.Suspense fallback={<p>Loading</p>}>
          <Switch>
            <Route path="/" exact component={Button} />
            <Route path="/topic/:topicNumber" component={Topic} />
            <Route path="/quiz/:topicNumber" component={Quiz} />
          </Switch>
        </React.Suspense>
      </div>
    </Router>
  );

  // return (
  //     <>
  //     <div>
  //     <Router>
  //     <Sidebar></Sidebar>
  //     </Router>

  //     </div>
  //     </>
  // )
}

export default App;
